
  .authContainer {
    display: flex;
    width: 100%;
    max-width: 1200px;
    border-radius: 30px;
    overflow: hidden;
    box-shadow: 8px 8px 15px var(--shadow-dark),
                -8px -8px 15px var(--shadow-light);
    margin: 20px auto;
  }
  
  .authHero {
    flex: 1;
    background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
                url('https://images.unsplash.com/photo-1499678329028-101435549a4e');
    background-size: cover;
    background-position: center;
    color: white;
    padding: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .authHero h1 {
    font-size: 3rem;
    margin-bottom: 1.5rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  }
  
  .authHero p {
    font-size: 1.2rem;
    margin-bottom: 2rem;
    line-height: 1.6;
  }
  
  .authFeatures {
    margin-top: 2rem;
  }
  
  .authFeature {
    display: flex;
    align-items: center;
    margin-bottom: 1.5rem;
  }
  
  .authFeature i {
    font-size: 1.5rem;
    margin-right: 1rem;
    color: var(--accent);
  }
  
  .authContent {
    flex: 1;
    background: var(--bg-light);
    padding: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .logo {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
  }
  
  .logo i {
    font-size: 2rem;
    margin-right: 10px;
    color: var(--primary);
  }
  
  .logo span {
    font-size: 1.8rem;
    font-weight: 700;
    color: var(--dark);
  }
  
  .authTabs {
    display: flex;
    border-radius: 15px;
    padding: 5px;
    background: var(--bg-light);
    box-shadow: inset 3px 3px 6px var(--shadow-dark),
                inset -3px -3px 6px var(--shadow-light);
    margin-bottom: 2rem;
  }
  
  .authTab {
    flex: 1;
    text-align: center;
    padding: 15px;
    cursor: pointer;
    font-weight: 600;
    color: var(--dark);
    border-radius: 10px;
    transition: all 0.3s ease;
  }
  
  .authTab.active {
    background: var(--bg-light);
    color: var(--primary);
    box-shadow: 3px 3px 6px var(--shadow-dark),
                -3px -3px 6px var(--shadow-light);
  }
  
  .authForm {
    display: none;
  }
  
  .authForm.active {
    display: block;
  }
  
  .formGroup {
    margin-bottom: 1.5rem;
  }
  
  .formGroup label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: 500;
    color: var(--dark);
  }
  
  .formControl {
    width: 100%;
    padding: 15px 20px;
    border: none;
    border-radius: 15px;
    font-size: 1rem;
    background: var(--bg-light);
    box-shadow: inset 3px 3px 6px var(--shadow-dark),
                inset -3px -3px 6px var(--shadow-light);
    transition: all 0.3s ease;
  }
  
  .formControl:focus {
    outline: none;
    box-shadow: inset 1px 1px 3px var(--shadow-dark),
                inset -1px -1px 3px var(--shadow-light);
  }
  
  .formRow {
    display: flex;
    gap: 3.5rem;
  }
  
  .formRow .formGroup {
    flex: 1;
  }
  
  .btn {
    width: 100%;
    padding: 15px;
    border: none;
    border-radius: 15px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-bottom: 1rem;
  }
  
  .btnPrimary {
    background: var(--primary);
    color: white;
    box-shadow: 5px 5px 10px var(--shadow-dark),
                -5px -5px 10px var(--shadow-light);
  }
  
  .btnPrimary:hover {
    box-shadow: 2px 2px 5px var(--shadow-dark),
                -2px -2px 5px var(--shadow-light);
  }
  
  .btnOutline {
    background: var(--bg-light);
    color: var(--primary);
    box-shadow: 5px 5px 10px var(--shadow-dark),
                -5px -5px 10px var(--shadow-light);
  }
  
  .btnOutline:hover {
    box-shadow: 2px 2px 5px var(--shadow-dark),
                -2px -2px 5px var(--shadow-light);
  }
  
  .divider {
    display: flex;
    align-items: center;
    color: var(--dark);
    margin: 2rem 0;
  }
  
  .divider::before,
  .divider::after {
    content: "";
    flex: 1;
    height: 1px;
    background: var(--shadow-dark);
    margin: 0 10px;
  }
  
  .socialLogin {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 1rem;
  }
  
  .socialBtn {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    color: white;
    box-shadow: 5px 5px 10px var(--shadow-dark),
                -5px -5px 10px var(--shadow-light);
    transition: all 0.3s ease;
  }
  
  .socialBtn:hover {
    transform: translateY(-3px);
    box-shadow: 7px 7px 14px var(--shadow-dark),
                -7px -7px 14px var(--shadow-light);
  }
  
  .btnGoogle { background: #DB4437; }
  .btnFacebook { background: #4267B2; }
  .btnTwitter { background: #1DA1F2; }
  
  .authFooter {
    text-align: center;
    color: var(--dark);
  }
  
  .authFooter a {
    color: var(--primary);
    text-decoration: none;
    font-weight: 500;
  }
  
  .forgotPassword {
    text-align: right;
    margin-top: -1rem;
    margin-bottom: 1.5rem;
  }
  
  .forgotPassword a {
    color: var(--primary);
    text-decoration: none;
    font-size: 0.9rem;
  }
  
  @media (max-width: 992px) {
    .authContainer {
      flex-direction: column;
    }
    .authHero, .authContent {
      padding: 40px;
    }
    .authFeature {
      justify-content: center;
    }
  }
  
  @media (max-width: 576px) {
    .authHero h1 {
      font-size: 2.2rem;
    }
    .formRow {
      flex-direction: column;
      gap: 1rem;
    }
  }
  